.categories li {
  display: inline-block;
  background: #f2f2f2;
  border-radius: 50px;
  padding: 10px 20px;
  margin-bottom: 25px;
  margin-right: 20px;

  border: 1px solid transparent;
  transition: all 0.3s ease;
}

.categories li:hover {
  cursor: pointer;
  border-color: silver;
  transform: scale(1.02);
}

.categories li.active {
  background: rgb(32, 32, 32);
  color: white;
}
.categories {
  display: flex;
  overflow: scroll;
}

@media (max-width: 500px) {
  .categories::-webkit-scrollbar {
    width: 0;
    display: none;
  }
  .categories li {
    padding: 10px 20px;
    margin-bottom: 0px;
    margin-right: 10px;
  }
}
